.footer {
    padding: 30px 0;
    border-top: 1px solid $border-white;
    
    &__body {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include media($sm){
            flex-direction: column;
        }

    }

    &__item {
        display: flex;
        align-items: center;
        
        @include media($sm){
            width: 100%;
            justify-content: space-between;
            margin-bottom: 30px;
        }
        @include media($xs){
            flex-direction: column;
            text-align: center;
        }
    }
    
    &__ensurance {
        margin-left: 20px;
        font-size: 13px;
        
        @include media($xs){
            margin: 15px 0;
        }
    }

    &__design {
        margin-right: 20px;
        font-weight: 600;
        font-size: 13px;

        @include media($xs){
            margin: 15px 0;
        }
    }
}

.icon-phone {
    // display: block;
    width: 60px;
    height: 60px;
    background: $nav-bg;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transform: rotate(-30deg);
    position: fixed;
    right: 30px;
    bottom: 30px;
    box-shadow: 0 0 10px $nav-bg;
    animation: phoneShaking 1s infinite ease-in-out;
    transition: 0.5s;

    @keyframes phoneShaking {
        0% {
            transform: rotate(-30deg);
            
        }
        50% {
            transform: rotate(-40deg);
        }
        100% {
            transform: rotate(-30deg);
        }
        
    }


    &:hover {
        animation: none;
        box-shadow: 0 0 20px $nav-bg;
        transform: scale(1.3);
    }

    
    
    &::before {
        font-size: 30px;
        color: $white;
    }

}