*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: $dark-font;
    // outline: 1px solid red;
}

a{
    text-decoration: none;
}

img {
    display: block;
}

ul {
    list-style: none;
}

body {
    font-family: 'Montserrat';
    font-weight: 500;
    // background: $white;
    // background: red;

    &.lock {
        overflow: hidden;
        
    }
}

body.lock {
    overflow: hidden;
}
body.noscroll {
    overflow: hidden;
}

.container {
    @include container();
}

.row {
    @include row();
}

.yellow {
    background: $yellow-btn;
}

.blue {
    background: $nav-bg;
}

.green {
    background: $green-btn;
}

.purple {
    background: $purple-btn;
}

::after, ::before {
    font-family: 'fontawesome';
}


.hide {
    display: none !important;
}

.flex {
    display: flex !important;
}

.show {
    display: block !important;
}
.fade {
    animation: fade 1s linear;
}

@keyframes fade {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.icon {
    &-call::before{ 
        content: "\f879";
        color: $black;
        
    }
    &-location::before{ 
        content: "\f3c5";
        color: $black;
        
    }
    &-arrow::before{
        content: "\f078";
        color: $black;
        font-size: 10px;
    }
    &-quest::before{
        content: "\f128";
        color: $black;
    }
    &-facebook::before{
        content: "\f39e";
        color: $black;
    }
    &-vk::before{
        content: "\f189";
        color: $black;
    }
    &-insta::before{
        content: "\f16d";
        color: $black;
    }
    &-bottom::before{
        content: "\f063";
        color: $black;
    }
    &-phone::before{
        content: "\f2a0";
        color: $black;
    }
    





}