.banner {
    background: url('./../images/banner_bg.png') center center / cover  no-repeat;
    // height: 85vh;
    padding-top: 270px;

    @media(max-width:1920px){
        padding-top: calc(50px + (270 - 50)*((100vw - 320px)/(1920 - 320)));
    }
    

    &.active {
        margin-top: 60px;
    }

    .container {
        height: 100%;
    }

    &__body {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }


    &__slide {
        text-align: center;
        // height: 100%;
        // padding-top: 150px;
        // margin-top: 100px;
        
            .slide__title {
                color: $white;
                font-size: 55px;
                font-weight: 600;
                text-transform: uppercase;
                margin-bottom: 30px;

                @include media($lg){
                    font-size: calc(20px + (55 - 20)*((100vw - 320px)/(1170 - 320)));
                    margin-bottom: calc(15px + (30 - 15)*((100vw - 320px)/(1170 - 320)));
                }

            }

            .slide__text {
                font-size: 30px;
                color: $white;
                text-transform: uppercase;
                margin-bottom: 30px;

                @include media($lg){
                    font-size: calc(14px + (30 - 14)*((100vw - 320px)/(1170 - 320)));
                }
            }

            .slide__btn {
                padding: 20px 25px;
                background: $green-btn;
                color: $white;
                display: inline-block;
                text-transform: uppercase;
                border-radius: 5px;
                font-size: 15px;

                @include media($lg){
                    padding: calc(15px + (20 - 15)*((100vw - 320px)/(1170 - 320))) calc(10px + (25 - 10)*((100vw - 320px)/(1170 - 320)));
                    font-size: calc(10px + (15 - 10)*((100vw - 320px)/(1170 - 320)));
                }
            }


    }


    .slick-list {
        overflow: hidden;
        width: 100%;
    }
    /* Лента слайдов */
    .slick-track{
        display: flex;
    }

    .slick-slider {
        flex-direction: column;
    }

    .slick-dots {
        margin: 40px 0;
        display: flex;
        justify-content: center;

        @include media($lg){
            margin: calc(15px + (40 - 15)*((100vw - 320px)/(1170 - 320)));
        }

        li.slick-active button::before{
            background: red;
        }
        
        li button{ 
            border: none;
            font-size: 0;
            background: none;

            &::before {
                content: '';
                width: 10px;
                height: 10px;
                background: $white;
                display: block;
                margin:0 5px;
                border-radius: 50%;
                // font-size: 30px;
            }
        }
    }

    .green {
        background: $green-btn;
    }
    
    .yellow {
        background: $yellow-btn;
    }
    .red {
        background: $red-btn;
    }



}

.tab {
    width: 100%;
    height: 175px;
    background: $nav-bg;
    display: flex;
    flex-direction: column;

    &__block {
        width: 100%;
    }


    &__links {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 66px;
        border-bottom: 1px solid $border-white;
        padding: 0 30px;
    }

    &__list {
        display: flex;
    }

    &__list li{
        margin-right: 30px;

    }

    &__link {
        color: $noactive-btn;
        text-transform: uppercase;
        font-size: 16px;
        position: relative;
        transition: all linear 1s;
        

    }

    &__link::after {
        content: '';
        transition: all linear 0.3s;
    }

    &__link:hover {
        color: $white;

        &::after {
            content: '';
            height: 3px;
            width: calc(100% - 10px);
            background: red;
            position: absolute;
            left: 5px;
            bottom: -25px;
            opacity: 1;
        }

    }

    &__link.active {
        color: $white;
        position: relative;

        &::after {
            content: '';
            display: block;
            position: absolute;
            left: 20px;
            bottom: -25px;
            height: 3px;
            width: calc(100% - 40px);
            background: $red-btn;

        }
    }

    &__ask {
        color: $white;

    }

    &__ask icon::before {
        color: $white;
        margin-right: 10px;
    }


    &__body {
        padding: 0 30px;
        // height: 100%;
        display: flex;
        align-items: center;
        // justify-content: space-between;
        // width: 100%;
        // flex: 1 1 100%;
        flex-grow: 1;
    }

    .input__dropdown {
        height: 44px;
        border-radius: 5px;
        border: none;
        outline: none;
        padding-left: 10px;

    }

    .input__text {
        height: 44px;
        // display: block;
        // width: 460px;
        // flex-grow: 1;
        width: 100%;
        outline: none;
        border: none;
        border-radius: 5px;
        margin-left: 10px;
        padding-left: 10px;

    }

    &__continue {
        height: 44px;
        background: $red-btn;
        border-radius: 5px;
        color: $white;
        text-transform: uppercase;
        padding: 15px 25px;
        margin-left: 10px;
        // display: flex;
        // justify-content: center;
        // align-items: center;

    }

    &__block-link {
        margin-left: 10px;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size: 13px;
        color: $white;

    }

    &__block-item {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
    }





}


.banner__tab {
    @include media($md){
        display: none;
    }
}


