.insurance {
    position: relative;
    border-top: 1px solid $border-white;


    &__body {
        padding: 40px 0;
    }

    &__main-title {
        font-size: 25px;
        font-weight: 600;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 45px;
    }

    &__main .row {
        @include media($md){
            justify-content: space-between;
            // text-align: center;
        }
        @include media($xs){
            justify-content: center;
            text-align: center;
        }
    }

    &__item {
        @include col(3);

        @include media($md){
            @include col(6);
        }
        @include media($xs){
            @include col(10);
            margin-bottom: 30px;
        }

    }

    &__title {
        color: $nav-bg;
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 30px;

    }

    &__link {
        font-size: 13px;
        display: inline-block;
        margin-bottom: 20px;
        // line-height: 35px;

        &:hover {
            color: $red-btn;
        }
    }


}



.icon-bottom::before {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    box-shadow: 0 0px 14px rgba(202, 202, 202, 0.5) ;
    position: absolute;
    left: calc(50% - 22px);
    top: -22px;
    background: $white;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $red-btn;
    transition: 0.5s;
    animation: keyDown 2s infinite ease-in-out;
    
    @keyframes keyDown {
        0% {
            // transform: translate(0, -5px);
            top: -27px;
            // background: white;
        }
        50% {
            // transform: translate(0, 5px);
            // background: rgb(218, 218, 218);
            top: -17px;
        }
        100% {
            // transform: translate(0, -5px);
            // background: white;
            top: -27px;
        }
    }
                    
}

.icon-bottom.rotate::before {
    transform: rotate(-180deg);
    // background: red;

}
