* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #23282f;
}

a {
  text-decoration: none;
}

img {
  display: block;
}

ul {
  list-style: none;
}

body {
  font-family: 'Montserrat';
  font-weight: 500;
}

body.lock {
  overflow: hidden;
}

body.lock {
  overflow: hidden;
}

body.noscroll {
  overflow: hidden;
}

.container {
  max-width: 1170px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1170px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.yellow {
  background: #fcbf2c;
}

.blue {
  background: #3768e3f6;
}

.green {
  background: #9ac446;
}

.purple {
  background: #85529c;
}

::after, ::before {
  font-family: 'fontawesome';
}

.hide {
  display: none !important;
}

.flex {
  display: flex !important;
}

.show {
  display: block !important;
}

.fade {
  animation: fade 1s linear;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.icon-call::before {
  content: "\f879";
  color: black;
}

.icon-location::before {
  content: "\f3c5";
  color: black;
}

.icon-arrow::before {
  content: "\f078";
  color: black;
  font-size: 10px;
}

.icon-quest::before {
  content: "\f128";
  color: black;
}

.icon-facebook::before {
  content: "\f39e";
  color: black;
}

.icon-vk::before {
  content: "\f189";
  color: black;
}

.icon-insta::before {
  content: "\f16d";
  color: black;
}

.icon-bottom::before {
  content: "\f063";
  color: black;
}

.icon-phone::before {
  content: "\f2a0";
  color: black;
}

.ad {
  background: #3768e3f6;
}

.ad__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 40px;
}

@media (max-width: 576px) {
  .ad__body {
    display: none;
  }
}

@media (max-width: 992px) {
  .ad__clients {
    display: flex;
    flex-direction: column;
  }
}

.ad__client {
  margin-right: 40px;
  color: white;
  font-size: 14px;
  line-height: 24px;
}

@media (max-width: 992px) {
  .ad__client {
    font-size: 10px;
    line-height: 10px;
    margin: 5px 0;
  }
}

.ad__client:last-child {
  margin: 0;
}

.ad-adress__item {
  color: white;
  font-size: 14px;
  margin-left: 40px;
}

@media (max-width: 992px) {
  .ad-adress__item {
    font-size: 10px;
    margin-left: calc(15px + (40 - 15)*((100vw - 320px)/(1170 - 320)));
  }
}

.ad-adress__item icon::before {
  color: white;
  font-size: 14px;
  margin-right: 12px;
}

.ad-adress {
  display: flex;
}

@media (max-width: 992px) {
  .ad-adress {
    text-align: right;
  }
}

.ad-adress li {
  position: relative;
}

@media (max-width: 992px) {
  .ad-adress li {
    margin: 0px 0;
  }
}

.ad-adress__list {
  position: absolute;
  right: 20px;
  transform: translate(0, 15px);
  background: black;
  padding: 10px;
  transition: 0.3s;
  opacity: 0;
  visibility: hidden;
}

.ad-adress__list.active {
  transform: translate(0, 5px);
  opacity: 1;
  visibility: visible;
}

.ad-adress__sub-item {
  color: white;
  padding: 5px 0;
  display: inline-block;
}

.ad .icon-arrow {
  display: inline-block;
  margin: 0;
  transition: 0.3s;
}

.ad .icon-arrow::before {
  margin: 0;
}

.ad-adress_active.active .icon-arrow {
  transform: rotate(-180deg);
}

.nav {
  transition: 0.5s;
  background: white;
  box-shadow: 0 8px 14px rgba(202, 202, 202, 0.5);
}

.nav__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  transition: 0.3s;
}

@media (max-width: 992px) {
  .nav__body {
    height: calc(40px + (80 - 40)*((100vw - 320px)/(1170 - 320)));
  }
}

.nav__logo {
  width: 160px;
}

@media (max-width: 1170px) {
  .nav__logo {
    width: calc(80px + (160 - 80)*((100vw - 320px)/(1170 - 320)));
  }
}

.nav__logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.nav__menu {
  display: flex;
  align-items: center;
}

@media (max-width: 992px) {
  .nav__menu {
    z-index: 10;
    position: fixed;
    left: 0;
    top: -100%;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.685);
    flex-direction: column;
    padding-top: 70px;
    overflow-y: scroll;
    transition: 0.5s;
  }
}

.nav__menu.active {
  top: 0;
}

.nav__list {
  display: flex;
}

@media (max-width: 992px) {
  .nav__list {
    flex-direction: column;
    text-align: center;
  }
}

@media (max-width: 768px) {
  .nav__list {
    width: 100%;
  }
}

.nav__list li {
  position: relative;
}

.nav__link {
  margin-right: 22px;
  font-size: 14px;
  line-height: 48px;
}

@media (max-width: 992px) {
  .nav__link {
    margin: 0;
    color: white;
  }
}

@media (max-width: 992px) {
  .nav__link .icon-arrow::before {
    color: white;
  }
}

.nav__profil {
  transition: 0.3s;
  padding: 15px;
  background: #fe0000;
  color: white;
  border-radius: 5px;
}

@media (max-width: 992px) {
  .nav__profil {
    padding: calc(5px + (15 - 5)*((100vw - 320px)/(1170 - 320)));
    font-size: 14px;
  }
}

.nav__sub-menu {
  position: absolute;
  left: -50%;
  transform: translate(0, 10px);
  background: black;
  width: 500px;
  color: white;
  padding: 10px 15px;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  z-index: 3;
}

@media (max-width: 992px) {
  .nav__sub-menu {
    position: static;
    display: none;
    animation: displayToNone 0.3s ease-in forwards;
    opacity: unset;
    visibility: unset;
  }
}

@media (max-width: 768px) {
  .nav__sub-menu {
    width: 100%;
  }
}

.nav__sub-menu.active {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0px);
  display: block;
  animation: displayFromNone 0.3s ease-in forwards;
  will-change: display , transform;
}

@media (max-width: 992px) {
  @keyframes displayFromNone {
    0% {
      display: none;
      transform: translate(0, -10%);
      transform: scale(1, 0);
      transform-origin: top;
    }
    1% {
      display: block;
    }
    100% {
      display: block;
      transform: translate(0, 0);
      transform-origin: top;
      transform: scale(1, 1);
    }
  }
}

.nav__sub-menu a {
  color: white;
  margin: 5px 0;
  display: inline-block;
}

.nav .icon-arrow {
  margin-left: 10px;
  display: inline-block;
  transform: rotate(0deg);
}

.nav .icon-arrow.active {
  transform: rotate(-180deg);
}

.nav__sub-link {
  font-size: 14px;
}

.nav__burger {
  width: 30px;
  height: 20px;
  position: relative;
  display: none;
  z-index: 10;
}

@media (max-width: 992px) {
  .nav__burger {
    display: block;
  }
}

.nav__burger span {
  width: 100%;
  height: 2px;
  position: absolute;
  left: 0;
  top: 9px;
  background: black;
  transition: 0.3s;
}

.nav__burger::before, .nav__burger::after {
  width: 100%;
  content: '';
  height: 2px;
  position: absolute;
  left: 0;
  background: black;
  transition: 0.3s;
}

.nav__burger::before {
  top: 0;
}

.nav__burger::after {
  bottom: 0;
}

.nav__burger.active span {
  visibility: hidden;
  background: white;
  transform: scale(0, 1);
  transform-origin: center;
}

.nav__burger.active::after {
  transform: rotate(-45deg);
  background: white;
  top: 9px;
  transition: 0.5s;
}

.nav__burger.active::before {
  transform: rotate(45deg);
  background: white;
  top: 9px;
  transition: 0.5s;
}

.nav__panel {
  display: none;
  text-align: center;
  margin-bottom: 20px;
}

@media (max-width: 576px) {
  .nav__panel {
    display: block;
  }
}

.nav__panel icon::before {
  color: white;
  padding: 0 5px;
  font-size: 10px;
}

.nav.active {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
}

.nav.active .nav__body {
  height: 60px;
}

.nav.active .nav__profil {
  padding: 5px 10px;
}

.popup {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all 0.5s ease 0s;
  z-index: 900;
}

.popup.open {
  opacity: 1;
  visibility: visible;
}

.popup.open .popup__content {
  transform: perspective(0) translate(0, 0) rotateX(0);
  opacity: 1;
}

.popup__area {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.popup__body {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 10px;
}

.popup__content {
  background: white;
  color: black;
  padding: 30px;
  position: relative;
  opacity: 0;
  transition: all 0.5s ease 0s;
  transform: perspective(600px) translate(0, -100%) rotateX(50deg);
}

.popup__title {
  font-size: 40px;
  margin-bottom: 1em;
}

.popup__close {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 20px;
}

.popup__content_image {
  padding: 0;
}

.popup__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.cabinet input {
  display: block;
  height: 40px;
  padding: 10px 10px;
  border: 1px solid rgba(184, 184, 184, 0.5);
  margin: 10px 0;
}

.cabinet .cabinet__link {
  display: inline-block;
  width: 100%;
  padding: 10px;
  text-align: center;
  background: #fe0000;
  color: white;
  border-radius: 4px;
}

.banner {
  background: url("./../images/banner_bg.png") center center/cover no-repeat;
  padding-top: 270px;
  /* Лента слайдов */
}

@media (max-width: 1920px) {
  .banner {
    padding-top: calc(50px + (270 - 50)*((100vw - 320px)/(1920 - 320)));
  }
}

.banner.active {
  margin-top: 60px;
}

.banner .container {
  height: 100%;
}

.banner__body {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.banner__slide {
  text-align: center;
}

.banner__slide .slide__title {
  color: white;
  font-size: 55px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 30px;
}

@media (max-width: 1170px) {
  .banner__slide .slide__title {
    font-size: calc(20px + (55 - 20)*((100vw - 320px)/(1170 - 320)));
    margin-bottom: calc(15px + (30 - 15)*((100vw - 320px)/(1170 - 320)));
  }
}

.banner__slide .slide__text {
  font-size: 30px;
  color: white;
  text-transform: uppercase;
  margin-bottom: 30px;
}

@media (max-width: 1170px) {
  .banner__slide .slide__text {
    font-size: calc(14px + (30 - 14)*((100vw - 320px)/(1170 - 320)));
  }
}

.banner__slide .slide__btn {
  padding: 20px 25px;
  background: #9ac446;
  color: white;
  display: inline-block;
  text-transform: uppercase;
  border-radius: 5px;
  font-size: 15px;
}

@media (max-width: 1170px) {
  .banner__slide .slide__btn {
    padding: calc(15px + (20 - 15)*((100vw - 320px)/(1170 - 320))) calc(10px + (25 - 10)*((100vw - 320px)/(1170 - 320)));
    font-size: calc(10px + (15 - 10)*((100vw - 320px)/(1170 - 320)));
  }
}

.banner .slick-list {
  overflow: hidden;
  width: 100%;
}

.banner .slick-track {
  display: flex;
}

.banner .slick-slider {
  flex-direction: column;
}

.banner .slick-dots {
  margin: 40px 0;
  display: flex;
  justify-content: center;
}

@media (max-width: 1170px) {
  .banner .slick-dots {
    margin: calc(15px + (40 - 15)*((100vw - 320px)/(1170 - 320)));
  }
}

.banner .slick-dots li.slick-active button::before {
  background: red;
}

.banner .slick-dots li button {
  border: none;
  font-size: 0;
  background: none;
}

.banner .slick-dots li button::before {
  content: '';
  width: 10px;
  height: 10px;
  background: white;
  display: block;
  margin: 0 5px;
  border-radius: 50%;
}

.banner .green {
  background: #9ac446;
}

.banner .yellow {
  background: #fcbf2c;
}

.banner .red {
  background: #fe0000;
}

.tab {
  width: 100%;
  height: 175px;
  background: #3768e3f6;
  display: flex;
  flex-direction: column;
}

.tab__block {
  width: 100%;
}

.tab__links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 66px;
  border-bottom: 1px solid rgba(184, 184, 184, 0.5);
  padding: 0 30px;
}

.tab__list {
  display: flex;
}

.tab__list li {
  margin-right: 30px;
}

.tab__link {
  color: #c2d3ff;
  text-transform: uppercase;
  font-size: 16px;
  position: relative;
  transition: all linear 1s;
}

.tab__link::after {
  content: '';
  transition: all linear 0.3s;
}

.tab__link:hover {
  color: white;
}

.tab__link:hover::after {
  content: '';
  height: 3px;
  width: calc(100% - 10px);
  background: red;
  position: absolute;
  left: 5px;
  bottom: -25px;
  opacity: 1;
}

.tab__link.active {
  color: white;
  position: relative;
}

.tab__link.active::after {
  content: '';
  display: block;
  position: absolute;
  left: 20px;
  bottom: -25px;
  height: 3px;
  width: calc(100% - 40px);
  background: #fe0000;
}

.tab__ask {
  color: white;
}

.tab__ask icon::before {
  color: white;
  margin-right: 10px;
}

.tab__body {
  padding: 0 30px;
  display: flex;
  align-items: center;
  flex-grow: 1;
}

.tab .input__dropdown {
  height: 44px;
  border-radius: 5px;
  border: none;
  outline: none;
  padding-left: 10px;
}

.tab .input__text {
  height: 44px;
  width: 100%;
  outline: none;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding-left: 10px;
}

.tab__continue {
  height: 44px;
  background: #fe0000;
  border-radius: 5px;
  color: white;
  text-transform: uppercase;
  padding: 15px 25px;
  margin-left: 10px;
}

.tab__block-link {
  margin-left: 10px;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 13px;
  color: white;
}

.tab__block-item {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 992px) {
  .banner__tab {
    display: none;
  }
}

.content__list {
  height: 80px;
  border-bottom: 1px solid rgba(184, 184, 184, 0.5);
  display: flex;
  align-items: center;
}

@media (max-width: 768px) {
  .content__list {
    justify-content: center;
  }
}

.content__link {
  margin-left: 35px;
  text-transform: uppercase;
  color: rgba(184, 184, 184, 0.5);
  font-weight: 600;
  font-size: 16px;
  position: relative;
}

.content__link::after {
  content: '';
