}

.content__link:hover::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 3px;
  background: #fe0000;
  left: 0;
  bottom: -32px;
  opacity: 1;
}

@media (max-width: 1170px) {
  .content__link {
    font-size: calc(12px + (16 - 12)*((100vw - 320px)/(1170 - 320)));
    margin: 0;
    margin: 0 10px;
  }
}

.content__link.active {
  color: black;
}

.content__link.active::after {
  content: '';
  position: absolute;
  width: calc(100% - 40px);
  height: 3px;
  background: #fe0000;
  left: 20px;
  bottom: -32px;
}

.content__item {
  padding: 35px 0;
}

