  transform: rotate(-30deg);
  position: fixed;
  right: 30px;
  bottom: 30px;
  box-shadow: 0 0 10px #3768e3f6;
  animation: phoneShaking 1s infinite ease-in-out;
  transition: 0.5s;
}

@keyframes phoneShaking {
  0% {
    transform: rotate(-30deg);
  }
  50% {
    transform: rotate(-40deg);
  }
  100% {
    transform: rotate(-30deg);
  }
}

.icon-phone:hover {
  animation: none;
  box-shadow: 0 0 20px #3768e3f6;
  transform: scale(1.3);
}

