.content {


    &__list {
        height: 80px;
        border-bottom: 1px solid $border-white;
        display: flex;
        align-items: center;

        @include media($sm){
            justify-content: center;
        }
    }

    &__link {
        margin-left: 35px;
        text-transform: uppercase;
        color: $border-white;
        font-weight: 600;
        font-size: 16px;
        position: relative;
        

        &::after {
            content: '';
            transition: 0.5s;

        }

        &:hover::after{
            content: '';
            position: absolute;
            width: 100%;
            height: 3px;
            background: $red-btn;
            left: 0;
            bottom: -32px;
            opacity: 1;

        }

        @include media($lg){
            font-size: calc(12px + (16 - 12)*((100vw - 320px)/(1170 - 320)));
            margin: 0;
            margin: 0 10px;
        }
    }

    &__link.active {
        color: $black;

        &::after{
            content: '';
            position: absolute;
            width: calc(100% - 40px);
            height: 3px;
            background: $red-btn;
            left: 20px;
            bottom: -32px;

        }
    }

    &__item {
        padding: 35px 0;
    }
}

.eia {
    // display: none;
    // display: flex;
    align-items: stretch;

    @include media($md){
        flex-direction: column;
    }

    &__info {
        display: flex;
        flex-wrap: wrap;
        @include col(8);
        padding: 0;
        align-items: stretch;

        @include media($md){
            @include col(12);
            padding: 0;
        }


    }


    &__item {
        min-height: 218px;
        width: 100%;
        border: 1px solid $border-white;

        box-shadow: 0 8px 14px rgba(202, 202, 202, 0.5) ;
        padding: 30px;
        transition: 0.5s;
        // &:nth-last-col(){
        //     margin-bottom: 0;
        // }

        &:hover {
            // transform: translate3d(5px,5px,10px)
            transform: scale(1.05);
        }

        
    }


    &__map {
        width: 100%;
        height: 100%;
    }

    &__map-body {
        height: 100%;
        position: relative;
        box-shadow: 0 8px 14px rgba(202, 202, 202, 0.5) ;

    }

    &__map-block {
        @include col(4);

        @include media($md){
            @include col(12);
            margin-top: 30px;
            height: calc(200px + (400 - 200)*((100vw -320px)/(992 - 320)));
        }
    }

    &__title {
        font-size: 18px;
        font-weight: 600;

    }

    &__icons {
        margin: 30px 0 45px 0;
        display: flex;
        
        a::before {
            margin-left: 5px;
            width: 38px;
            height: 38px;
            color: rgb(39, 39, 110);
            box-shadow: 0 0 5px rgba(202, 202, 202, 0.5);
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &__links {
        display: flex;
        justify-content: space-between;

    }

    &__link {
        font-size: 13px;
        text-transform: capitalize;
    }

    &__share {
        color: $red-btn;
    }

    &__img {
        display: block;
        margin: 50px auto;
        max-width: 230px;

        img {
            width: 100%;
            height: 100%;
            object-fit: fill;

        }
    }

    &__text {
        font-size: 13px;
        margin-top: 25px;
        margin-bottom: 45px;
    }

    &__input-block {
        margin-top: 35px;
        display: flex;

        @include media($lg){
            flex-direction: column;
        }

    }

    &__input {
        padding-left: 20px;
        border: 1px solid $border-white;
        height: 50px;
    }

    &__send {
        width: 124px;
        height: 50px;
        background: $red-btn;
        border-radius: 2px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $white;
        text-transform: uppercase;
        font-size: 14px;
        font-weight: 600;

        @include media($lg){
            margin: 15px auto;

        }

    }

    &__find {
        display: inline-block;
        width: 170px;
        height: 55px;
        background: $nav-bg;
        border-radius: 3px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        color: $white;
        font-size: 14px;
        position: absolute;
        left: calc(50% - 85px) ;
        bottom: 40px;

    }


    &__col {
        @include col(6);
        display: flex;
        margin-bottom: 30px;

        @include media($md){
            @include col(6);
        }
        @include media($xs){
            @include col(12);
        }
    
        &:nth-child(3),&:nth-child(4){
            margin-bottom: 0;

            @include media($xs){
                margin-bottom: 30px;
            }
        }
    
    }

}









.offer {

    .row {
        @include media($sm){
            justify-content: center;
        }
    }


    &__col {
        @include col(6);

        @include media($sm){
            @include col(10);
        }
        @include media($xs){
            @include col(12);
        }
    }

    &__item {
        margin-bottom: 30px;
        box-shadow: 0 8px 14px rgba(202, 202, 202, 0.5);
        transition: 0.3s;

        &:hover {
            transform: scale(1.02);
        }

        

    }

    &__img {
        position: relative;
        height: 215px;

        @include media($lg){
            height: calc(170px + (215 - 170)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &__img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__icon {
        display: inline-block;
        padding: 10px 15px;
        font-size: 13px;
        font-weight: 600;
        text-transform: uppercase;
        color: $white;
        position: absolute;
        left: 20px;
        top: 20px;
        border-radius: 3px;


    }


    &__info {
        border: 1px solid $border-white;
        border-top: none;
        padding: 30px 30px 10px 30px;
        min-height: 115px;
        display: flex;
        justify-content: space-between;
        align-items: center;


        @include media($xs){
            flex-direction: column;
        }
    }

    &__more {
        @include media($xs){
            margin-top: 15px;
        }
    }

    &__title {
        font-size: 16px;
        font-weight: 600;
        margin-bottom: 15px;
    }

    &__text {
        font-size: 13px; 
    }

    &__link {
        font-size: 15px;
        font-weight: 600;
        color: $red-btn;
        text-transform: capitalize;
        text-align: right;
    }


    & .popup__content {
        padding: 0;

        .offer__item {
            margin: 0;
            background: $white;
        }
    }
}
