.ad {
    background: $nav-bg;
    

    &__body {
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 40px;

        @include media($xs){
            display: none;
        }
    }

    &__clients {
        @include media($md){
            display: flex;
            flex-direction: column;
        }
    }

    &__client {
        margin-right: 40px;
        color: $white;
        font-size: 14px;
        line-height: 24px;

        @include media($md){
            font-size: 10px;
            line-height: 10px;
            margin: 5px 0;
        }
    }

    &__client:last-child{
        margin: 0;
    }

    &-adress__item {
        color: $white;
        font-size: 14px;
        margin-left: 40px;
        

        @include media($md){
            font-size: 10px;
            margin-left: calc(15px + (40 - 15)*((100vw - 320px)/(1170 - 320)));

        }
    }
    &-adress__item icon::before{
        color: $white;
        font-size: 14px;
        margin-right: 12px;

    }

    &-adress {
        display: flex;

        @include media($md){
            // flex-direction: column;
            text-align: right;
        }
        
    }

    &-adress li {
        position: relative;

        @include media($md){
            margin: 0px 0;
        }
    }

    

    &-adress__list {
        position: absolute;
        right: 20px;
        // top: 120%;
        transform: translate(0, 15px);
        background: $black;
        padding: 10px;
        // display: none;
        transition: 0.3s;
        opacity: 0;
        visibility: hidden;
    }

    &-adress__list.active {
        // display: block;
        transform: translate(0, 5px);
        opacity: 1;
        visibility: visible;
    }

    &-adress__sub-item {
        color: $white;
        padding: 5px 0;
        display: inline-block;
    }

    .icon-arrow {
        // transform: rotate(0);
        display: inline-block;
        margin: 0;
        transition: 0.3s;
    }
    .icon-arrow::before {
        margin: 0;
    }

    &-adress_active.active .icon-arrow {
        transform: rotate(-180deg);

        
    }
}