.nav {
    
    transition: 0.5s;
    background: $white;
    box-shadow: 0 8px 14px rgba(202, 202, 202, 0.5);

    // &.active {
    //     position: fixed;
    //     top: 0;
    //     width: 100%;
    //     z-index: 20;
        
    //     .nav__body {
    //         height: 60px;
    //     }
    // }

    
    &__body {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 80px;
        transition: 0.3s;

        @include media($md){
            height: calc(40px + (80 - 40)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &__logo {
        // height: 40px;
        width: 160px;

        @include media($lg){
            // height: calc(20px + (40 - 20)*((100vw - 320px)/(1170 - 320)));
            width: calc(80px + (160 - 80)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &__logo img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }




    &__menu {
        display: flex;
        align-items: center;
        
        @include media($md){
            z-index: 10;
            // display: none;
            position: fixed;
            left: 0;
            top: -100%;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.685);
            flex-direction: column;
            padding-top: 70px;
            overflow-y: scroll;
            transition: 0.5s;

        }
        &.active {
            top: 0;
        }
    }

    &__list {
        display: flex;

        @include media($md){
            flex-direction: column;
            text-align: center;
        }

        @include media($sm){
            // flex: 1 1 100%;
            width: 100%;
        }
    }

    &__list li {
        position: relative;
    }

    &__link {
        margin-right: 22px;
        font-size: 14px;
        line-height: 48px;

        @include media($md){
            margin: 0;
            color: $white;
        }

        .icon-arrow::before {
            @include media($md){
                color: white;
            }
        }
    }

    &__profil {
        transition: 0.3s;
        padding: 15px;
        background: $red-btn;
        color: $white;
        border-radius: 5px;

        @include media($md){
            padding: calc(5px + (15 - 5)*((100vw - 320px)/(1170 - 320)));
            font-size: 14px;
        }
    }

    &__sub-menu {
        position: absolute;
        left: -50%;
        transform: translate(0, 10px);
        background: $black;
        width: 500px;
        color: $white;
        padding: 10px 15px;
        opacity: 0;
        visibility: hidden;
        transition: 0.3s;
        z-index: 3;

        @include media($md){
            position: static;
            display: none;
            animation: displayToNone 0.3s ease-in forwards;
            opacity: unset;
            visibility: unset;
            
        }
        @include media($sm){
            width: 100%;

        }
    }

    

    &__sub-menu.active {
        opacity: 1;
        visibility: visible;
        transform: translate(0,0px);
        display: block;
        animation: displayFromNone 0.3s ease-in forwards;
        will-change: display , transform;
    }

    @include media($md){
        @keyframes displayFromNone {
            0% {
                display: none;
                transform: translate(0, -10%);
                transform: scale(1, 0);
                transform-origin: top;
        //         // background: $black;
            }
            1% {
                display: block;
                // transform: translate(1, 1);
            }
    
            100% {
                display: block;
                transform: translate(0, 0);
                transform-origin: top;
                transform: scale(1,1);
        //         // background: red;
            }
        }
    }

    &__sub-menu a {
        color: $white;
        margin: 5px 0;
        display: inline-block;
    }

    & .icon-arrow {
        margin-left: 10px;
        display: inline-block;
        transform: rotate(0deg);
    }

    & .icon-arrow.active {
        transform: rotate(-180deg);
    }

    &__sub-link {
        font-size: 14px;
    }


    &__burger {
        width: 30px;
        height: 20px;
        position: relative;
        display: none;
        z-index: 10;
        // background: red;

        @include media($md){
            display: block;
        }
    }

    &__burger span {
        width: 100%;
        height: 2px;
        position: absolute;
        left: 0;
        top: 9px;
        background: $black;
        transition: 0.3s;
    }

    &__burger::before,&__burger::after {
        width: 100%;
        content: '';
        height: 2px;
        position: absolute;
        left: 0;
        background: $black;
        transition: 0.3s;
    }

    &__burger::before {
        top: 0;
    }
    &__burger::after {
        bottom: 0;
    }


    &__burger.active span {
        visibility: hidden;
        background: $white;
        // width: 0;
        transform: scale(0,1);
        transform-origin: center;
    }

    &__burger.active::after {
        transform: rotate(-45deg);
        background: $white;
        top: 9px;
        transition: 0.5s;

    }
    &__burger.active::before {
        transform: rotate(45deg);
        background: $white;
        top: 9px;
        transition: 0.5s;
    }



    &__panel {
        display: none;
        text-align: center;
        // margin: 20px 0;
        margin-bottom: 20px;


        @include media($xs){
            display: block;
        }

        & icon::before {
            // background: $white;
            color: $white;
            padding: 0 5px;
            font-size: 10px;
        }

    }


    


}
.nav.active {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999;
    // background: white;
    // margin-top: 80px;

    .nav__body {
        height: 60px;
    }

    .nav__profil {
        padding: 5px 10px;
    }
}













.popup {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    opacity: 0;
    visibility: hidden;
    overflow-y: auto;
    overflow-x: hidden;
    transition: all 0.5s ease 0s;
    z-index: 900;
}

.popup.open{
    opacity: 1;
    visibility: visible;

}

.popup.open .popup__content {
    transform: perspective(0) translate(0 , 0) rotateX(0);
    opacity: 1;
}

.popup__area {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;

}

.popup__body {
    min-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px 10px;
}

.popup__content {
    background: white;
    color: black;
    // max-width: 800px;
    padding: 30px;
    position: relative;
    opacity: 0;
    transition: all 0.5s ease 0s ;
    transform: perspective(600px) translate(0 , -100%) rotateX(50deg);
}

.popup__title {
    font-size: 40px;
    margin-bottom: 1em;
}

.popup__close {
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: 20px;
}

.popup__content_image {
    padding: 0;
}

.popup__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}




.cabinet {

    & input {
        display: block;
        height: 40px;
        padding: 10px 10px;
        border: 1px solid $border-white;
        margin: 10px 0;
    }

    & .cabinet__link {
        display: inline-block;
        width: 100%;
        padding: 10px;
        text-align: center;
        background: $red-btn;
        color: $white;
        border-radius: 4px;

    }
}