.news {
    background: $gray-bg;
    padding: 80px 0;
    border-top: 1px solid $border-white;


    &__main-title {
        text-transform: uppercase;
        font-weight: 600;
        font-size: 30px;
        text-align: center;
        margin-bottom: 35px;
    }

    &__tab {
        border-bottom: 1px solid $border-white;
        display: flex;
        justify-content: center;
        padding-bottom: 28px;
    }

    &__list {
        display: flex;

    }

    &__link {
        display: inline-block;
        margin: 0 18px;
        color: $border-white;
        font-size: 16px;
        font-weight: 600;
        text-transform: uppercase;
        position: relative;
        transition: 0.5s;

        @include media($lg){
            font-size: calc(12px + (16 - 12)*((100vw - 320px)/(1170 - 320)));
            margin: 0 calc(12px + (18 - 12)*((100vw -320px)/(1170 - 320)));
        }


    }

    &__link::after {
        content: '';
        transition: 0.5s;
    }

    &__link:hover::after {
        height: 3px;
        width: 100%;
        background: $red-btn;
        position: absolute;
        left: 0;
        bottom: -30px;
        opacity: 1;


    }

    &__link.active {
        color: $black;

        &::after {
            content: '';
            height: 3px;
            width: calc(100% - 30px);
            position: absolute;
            left: 15px;
            bottom: -30px;
            background: $red-btn;
        }
    }









    &__content {
        padding: 40px 0 0 0;

    }

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__item .row {
        justify-content: center
    }

    &__col {
        margin-bottom: 40px;
        @include col(4);

        @include media($md){
            @include col(6);
        }
        @include media($xs){
            @include col(12);
        }
    }

    &__card {
        box-shadow: 0 8px 14px rgba(202, 202, 202, 0.5) ;
        height: 100%;
        display: flex;
        flex-direction: column;
        transition: 0.3s;


        &:hover {
            transform: scale(1.02);
        }



    }

    &__img {
        height: 210px;

        @include media($lg){
            height: calc(170px + (210 - 170)*((100vw - 320px)/(1170 - 320)));
        }


        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__info {
        border: 1px solid $border-white;
        border-top: none;
        flex-grow: 1;
        padding: 30px 30px 20px 30px;
    }

    &__date {
        color: $gray;
        font-size: 13px;
        display: inline-block;
        margin-bottom: 20px;
    }

    &__title {
        font-size: 14px;
        font-weight: 600;

    }

    &-block__link {
        padding: 20px 30px;
        background: $red-btn;
        font-size: 14px;
        font-weight: 600;
        text-transform: uppercase;
        color: $white;
        display: inline-block;
    }


    


}