$Montserrat:('Montserrat','Medium', 500),
            ('Montserrat','SemiBold', 600),
            ('fontawesome','duotone', 900),
            ('fontawesome','regular', 400),
            ('fontawesome','solid', 900),
            ('fontawesome','light', 300),
            ('fontawesome','brands', 400),
            ;

@each $family, $name, $weight in $Montserrat {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }
}


$white: white;
$border-white: rgba(184, 184, 184, 0.5);
$black: black;
$gray-bg: #fafafa;
$nav-bg: #3768e3f6;
$red-btn: #fe0000;
$gray: #a5a5a5;
$noactive-btn: rgba(197, 197, 197, 0.877);
$dark-font: #23282f;
$green-btn: #9ac446;
$yellow-btn: #fcbf2c;
$purple-btn: #85529c;
$noactive-btn: #c2d3ff;
