.about {
    padding: 60px 0;
    background: $gray-bg;
    border-top: 1px solid $border-white;

    &__body {
        @include media($xs){
            justify-content: center;
        }
    }


    &__item {
        @include col(3);

        @include media($md){
            @include col(6);
            margin-bottom: 30px;
        }
        @include media($xs){
            @include col(10);
            text-align: center;
        }
    }


    &__block {
        font-size: 12px;
        color: $gray;
        display: inline-block;
    }

    &__numb {
        color: $nav-bg;
        font-size: 25px;
        font-weight: 600;
        margin-top: 20px;
        margin-bottom: 30px;

        @include media($lg){
            font-size: calc(16px + (25 - 16)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &__link {
        font-size: 13px;
        display: inline-block;
        margin-bottom: 20px;

        &:hover {
            color: $red-btn;
        }
    }

    .eia__icons {
        margin: 20px 0 40px 0;

        @include media($xs){
            justify-content: center;
        }
    }

    &__flex {
        display: flex;
        justify-content: space-between;
        margin-top: 20px;
    }



}